import React from 'react'
import { message } from 'antd'
import axios from 'axios'
import { useState } from 'react'
import Button from 'react-bootstrap/Button'
import Col from 'react-bootstrap/Col'
import Form from 'react-bootstrap/Form'
import Row from 'react-bootstrap/Row'
import { set } from 'mongoose'
const AddReceptionist = () => {
  const [name, setName] = useState('')
  const [email, setEmail] = useState('')
  const [uniqueId, setUniqueId] = useState('')
  const [mobileNumber, setMobileNumber] = useState('')
  const [gender, setGender] = useState('Male')
  const [age, setAge] = useState()
  const [address, setAddress] = useState('')
  const [zip, setZip] = useState('')

  const [validated, setValidated] = useState(false)

  // { name,email,uniqueId,mobileNumber,gender,age,address,zip }

  const handleSubmit = async (e) => {
    try {
      const form = e.currentTarget

      if (form.checkValidity() === false) {
        e.preventDefault()
        e.stopPropagation()
        setValidated(true)
        return
      }
      e.preventDefault()
      e.stopPropagation()
      try {
        const formData = {
          name,
          email,
          uniqueId,
          mobileNumber,
          gender,
          age,
          address,
          zip,
          password: mobileNumber,
        }
        const res = await axios.post('/api/v1/admin/addNewReceptionist', formData)
        if (res.data.success) {
          message.success('Receptionist Added Successfully')
          setName('')
          setEmail('')
          setUniqueId('')
          setMobileNumber('')
          setGender('Male')
          setAge('')
          setAddress('')
          setZip('')
          setValidated(false)
        }
      } catch (error) {
        message.error('Something went wrong')
        console.log(error)
      }
    } catch (error) {
      console.log(error)
    }
  }

  return (
    <div className="container justify-content-center d-flex mt-5 w-100 ">
      <Form noValidate validated={validated} onSubmit={handleSubmit} autoComplete="off">
        <Row className="mb-3">
          <Form.Group
            as={Col}
            className="col-sm-12 col-lg-4 col-xs-12 col-md-4 col-12"
            controlId="formFirstName"
          >
            <Form.Label>Name</Form.Label>
            <Form.Control
              type="text"
              placeholder="Enter First Name"
              onChange={(e) => {
                setName(e.target.value)
              }}
              value={name}
              required
            />
            <Form.Control.Feedback>Looks good!</Form.Control.Feedback>
          </Form.Group>
          <Form.Group
            as={Col}
            className="col-sm-12 col-lg-4 col-xs-12 col-md-4 col-12"
            controlId="formMobileNumber"
          >
            <Form.Label>Mobile Number</Form.Label>
            <Form.Control
              type="number"
              placeholder="Enter Mobile Number"
              onChange={(e) => setMobileNumber(e.target.value)}
              value={mobileNumber}
              required
            />
            <Form.Control.Feedback>Looks good!</Form.Control.Feedback>
          </Form.Group>

          <Form.Group
            as={Col}
            className="col-sm-12 col-lg-4 col-xs-12 col-md-4 col-12"
            controlId="formLastName"
          >
            <Form.Label>Unique Id</Form.Label>
            <Form.Control
              type="text"
              placeholder="Press Any key"
              onChange={(e) => setUniqueId(`AMN-REC-${mobileNumber.split('').slice(7).join('')}`)}
              value={uniqueId}
              required
            />
          </Form.Group>

          <Form.Group
            as={Col}
            className="col-sm-12 col-lg-4 col-xs-12 col-md-4 col-12 mt-md-3"
            controlId="formGender"
          >
            <Form.Label>Gender</Form.Label>
            <Form.Select onChange={(e) => setGender(e.target.value)} value={gender}>
              <option>Male</option>
              <option>Female</option>
            </Form.Select>
            <Form.Control.Feedback>Looks good!</Form.Control.Feedback>
          </Form.Group>
          <Form.Group
            as={Col}
            className="col-sm-12 col-lg-4 col-xs-12 col-md-4 col-12 mt-md-3"
            controlId="formAge"
          >
            <Form.Label>Age</Form.Label>
            <Form.Control
              type="number"
              placeholder="Enter Age"
              onChange={(e) => setAge(e.target.value)}
              value={age}
              required
            />
            <Form.Control.Feedback>Looks good!</Form.Control.Feedback>
          </Form.Group>
        </Row>

        <Row className="mb-3">
          <Form.Group
            as={Col}
            className="col-sm-12 col-lg-4 col-xs-12 col-md-4 col-12"
            controlId="formLastName"
          >
            <Form.Label>Email</Form.Label>
            <Form.Control
              type="text"
              className="disabled"
              placeholder="Enter Email"
              onChange={(e) => setEmail(e.target.value)}
              value={email}
              required
            />
            {/* <Form.Control type="text" placeholder="Enter Last Name" value={email} required /> */}
            <Form.Control.Feedback>Looks good!</Form.Control.Feedback>
          </Form.Group>
        </Row>

        <Form.Group className="mb-3" controlId="formGridAddress1">
          <Form.Label>Address</Form.Label>
          <Form.Control
            placeholder="1234 Main St"
            onChange={(e) => setAddress(e.target.value)}
            value={address}
            required
          />
          <Form.Control.Feedback>Looks good!</Form.Control.Feedback>
        </Form.Group>

        <Row className="mb-3">
          <Form.Group
            as={Col}
            controlId="formGridZip"
            className="col-4 col-sm-12 col-lg-4 col-xs-12 col-md-4 col-12"
          >
            <Form.Label>Zip</Form.Label>
            <Form.Control
              onChange={(e) => setZip(e.target.value)}
              value={zip}
              required
              placeholder="325501"
            />
            <Form.Control.Feedback>Looks good!</Form.Control.Feedback>
          </Form.Group>
        </Row>
        <Button
          variant="primary"
          className="col-3 col-sm-12 col-lg-3 col-xs-12 col-md-3 col-12 offset-md-9"
          type="submit"
        >
          Submit
        </Button>
      </Form>
    </div>
  )
}

export default AddReceptionist
